.pera{
    font-family: 
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    margin-top: 25px;
     margin-left: 255px; 
}
.navbar{
    position: fixed;
    background-color: white; padding: 0; margin: 0;
    width: 100%; height: 70px;
}
@media only screen and (max-width: 1115px)  {
    .pera{
        
         margin-left: 120px; 
    }
}
@media only screen and (max-width: 640px)  {
    .navbar-brand{        
         margin-left: 20px !important; 
    }
}
@media only screen and (max-width: 640px)  {
    .navbar-toggler-icon{        
         margin-right: 20px !important; 
    }
}
@media only screen and (max-width: 1060px)  {
    .pera{
        
         margin-left: 70px; 
    }
}
@media only screen and (max-width: 990px)  {
    .pera{
        
         margin-left: 0px; 
    }
}
@media only screen and (max-width: 990px)  {
    .inputform input{
        width: 100% !important;
        margin-bottom: 20px;
        border: 1px solid hsla(41, 100%, 53%, 1);
    } 
}
@media only screen and (max-width: 990px)  {
    .btmm{
        width: 100%;
        margin-right: 0px !important;
        margin-bottom: 20px;
    } 
}
.navbar-brand{
    margin-left: 90px;
    margin-top: 10px;
}
.search::-webkit-input-placeholder{
    color: hsla(41, 100%, 53%, 1);
    font-weight: bold;
    /* width: 50px; */
    font-family: 
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.search{
    border: none;
}
.inputform input{
    width: 130px;
}
.btmm{
    color: hsla(41, 100%, 53%, 1);
    background-color: white;
    border: none;
    font-weight: bold;
    margin-right: 90px;
    padding-left: 20px;
    padding-right: 20px;
    /* box-shadow: 0px 2px 40px  hsla(41, 100%, 53%, 1); */
    box-shadow: 5px 6px 15px 1px rgb(255, 218, 139);
}
.btmm:hover{
    background-color: hsla(41, 100%, 53%, 1);
    color: white;
}
.navbar-toggler-icon{
    margin-top: 10px;
    margin-right: 90px;
    border: none;
}
.navbar-toggler{
    border: none;
    color: white;
}