.neww{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-around;
    background-color: hsla(41, 100%, 53%, 1);
}
.imgg{
    margin-top: 140px;
    margin-left: 70px;
}
.textt{
    margin-top: 100px;
}
.headingg{
    color: white;
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: bold ;
    font-size: 60px;
    padding-left: 20px;
    padding-right: 20px;
}
.paragraphh{
    color: hsla(22, 0%, 31%, 1);
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    padding-left: 20px;
    padding-right: 80px;
}
.newsletterr{
    background-color: white;
    margin-left: 20px;
    margin-right: 20px;
    border-radius: 10px;
    padding: 20px 30px 20px 30px;
}
.deliveryy{
    font-weight: bold;
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   color: hsla(22, 88%, 55%, 1);
   background-color: rgba(241, 114, 40, 0.1);
   border: none; border-radius: 10px;
   padding: 5px 10px 5px 10px; margin-right: 10px;
}
.pickk{
    background-color: white; border: none; margin-right: 10px;
    font-weight: bold; border-radius: 10px; color: hsla(0, 0%, 46%, 1);
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   
}
.pickk:hover{
    background-color: rgba(241, 114, 40, 0.1); border: none;
    font-weight: bold; padding: 5px 10px 5px 10px; margin-right: 10px; color: hsla(0, 0%, 46%, 1);
    border-radius: 10px;
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   
}
.emaill {
    width: 75% ;
    background-color: #F5F5F5; border: none;
    padding: 5px 10px; border-radius: 10px;
}
.butns{
    font-weight: bold;
    font-family:
    'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   color: white;
   background-color: rgb(230, 75, 75);
   border: none; border-radius: 10px;
   padding: 5px 10px 5px 10px; margin-left: 10px;
}
@media only screen and (max-width: 1115px)  {
    .textt{
        margin-top: 50px;
    }
}
@media only screen and (max-width: 1115px)  {
    .imgg{
        margin-top: 50px;
        margin-left: 55px;
    }
}
@media only screen and (max-width: 415px)  {
    .imgg{
        margin-top: 50px;
        margin-left: 35px;
    }
}
@media only screen and (max-width: 532px)  {
    .emaill {
        width: 100% ;
        
    }
}
@media only screen and (max-width: 532px)  {
    .butns{
        margin-top: 10px; width: 100%; margin-left: 0px;
    }
}